<script>
  import { createEventDispatcher } from "svelte";
  const dispatch = createEventDispatcher();
  function mapClicked() {
    console.log("Dispatching basemap click...");
    dispatch("mapClicked", { });
  }
  // wrapper handler for activateButton when Enter or space key is pressed
  function onKeyUp(e) {
    if (e.key == "Escape") {
      mapClicked()
    }
  }
</script>

